@import '../../common/styles/sass/variables';
@import '../../common/styles/sass/mixins';


:root[data-theme="dark"] {
  --skillBackground: rgba(251, 252, 255, 0.923);
  --skillBoxShadow: inset 6px 4px 20px 0px rgb(0 129 255 / 9%);
}

:root[data-theme="light"] {
  --skillBackground: rgba(255, 255, 255, 0.1);
}


.skill {
  position: relative;
  width: 300px;
  margin-top: 60px;
  @include flex(column, flex-start, center, nowrap);

  @media screen and (max-width: 1128px) {
    width: 500px;
    margin-top: 90px;

    &:first-child {
      margin-top: 60px;
    }
  }

  @media screen and (max-width: 630px) {
    width: 100%;
  }

  @media screen and (max-width: 600px) {
    width: 100%;
  }

  .icon {
    position: relative;
    height: 80px;
    width: 80px;
    background: var(--skillBackground);
    // box-shadow: var(--boxShadow);
    transform: rotate(45deg);
    margin-bottom: 40px;
    @include flex(row, center, center, nowrap);

    transition: .5s;

    .svg {
      position: absolute;
      transform: rotate(-45deg);
    }

    img {
      transform: rotate(-45deg);
      width: 55%;
    }

    &:hover {
      box-shadow: 0 0 43px 1px $primaryColor;
      // box-shadow: 0 0 43px 1px #fff;
      transition: all 0.3s ease-in-out 0s;
    }
  }

  .skillTitle {
    @include text($TitleFontFamily, 16px, 1.2, 700);
    text-transform: uppercase;
    margin: 0 0 15px;
    padding: 0;
    letter-spacing: 1px;
    top: 125px;
  }

  .description {
    text-align: center;

  }
}