@import '../common/styles/sass/variables';
@import '../common/styles/sass/mixins';



:root[data-theme="dark"] {
    --footBorder: #4288ae;
    --hoverBorder: #4288ae;
}

:root[data-theme="light"] {
    --footBorder: #4a4a4a;
    --hoverBorder: #fff
}


.footer {
    min-height: 300px;
    @include flex(row, center, stretch, nowrap);

    .container {
        @include container();
        @include flex(column, flex-start, center, nowrap);
        padding: 100px 0 20px;

        .socialIcons {
            @include flex(row, space-between, center, nowrap);
            width: 400px;
            margin-top: 30px;

            @media screen and (max-width: 505px) {
                width: 100%;
            }

            .socialIcon {
                width: 60px;
                height: 60px;
                border: 1px solid var(--footBorder);
                border-radius: 50%;
                background-color: $bgPrimary;
                transition: 0.3s;

                &:hover {
                    box-shadow: 0 0 20px 1px $primaryColor;
                    transform: scale(1.1);
                    border: 1px solid var(--hoverBorder);
                    transition: all 0.3s ease-in-out 0s;
                }

                @media screen and (max-width: 380px) {
                    width: 50px;
                    height: 50px;
                }

                a {
                    width: 100%;
                    height: 100%;
                    @include flex(row, center, center, nowrap);

                    img {
                        width: 60%;
                    }
                }
            }
        }

        .copyright {
            margin-top: 10px;
        }
    }
}