@import './common/styles/sass/variables';


*{
    transition: all 0.02s;
}

.App {
    > div {
        &:nth-child(n) {
            background-color: $bgSecondary;
        }
        &:nth-child(2n) {
            background-color: $bgPrimary;
        }
    }
}

::-webkit-scrollbar {
    display: none;
}